        <v-img
          alt="Vuetify Logo"
          class="shrink mr-2"
          contain
          src="./assets/Bankling_logo.png"
          transition="scale-transition"
          width="40"
        />

        <h1 style="font-family: 'Questrial', sans-serif;">BANKLING</h1>
      </div>
      <div v-if="logged" style="margin-left: 20px">
        <v-btn large v-bind:style="fontSize" text to="/"
          ><span class="mr-2">Inicio</span> <v-icon> mdi-home</v-icon></v-btn
        >
        <v-btn large v-bind:style="fontSize" text to="/crear"
          ><span class="mr-2">Añadir movimiento</span>
          <v-icon> mdi-pencil</v-icon></v-btn
        >
        <v-btn large v-bind:style="fontSize" text to="/graficos"
          ><span class="mr-2">Gráficos</span>
          <v-icon> mdi-chart-line</v-icon></v-btn
        >
        <v-btn large v-bind:style="fontSize" text to="/historial"
          ><span class="mr-2">Historial</span>
          <v-icon> mdi-folder-table</v-icon></v-btn
        >
        <v-btn large v-bind:style="fontSize" text to="/ayuda"
          ><span class="mr-2">Ayuda</span>
          <v-icon> mdi-help-circle</v-icon></v-btn
        >
      </div>

      <v-spacer></v-spacer>
      <div v-if="logged">
        <v-btn large v-bind:style="fontSize" text to="/opciones"
          ><span class="mr-2">Opciones</span> <v-icon> mdi-cog</v-icon>
        </v-btn>
        <v-btn large v-bind:style="fontSize" text>
          <span class="mr-2" @click="exit">Cerrar sesión</span>
          <v-icon>mdi-login</v-icon>
        </v-btn>
      </div>
    </v-app-bar>

    <v-main>
      <router-view />
    </v-main>
    <v-footer
      color="primary"
      dark
      class="py-4 text-center justify-center white--text"
