          </div>
        </v-row>
        <v-row class="d-flex justify-space-around">
          <div class="text-center mt-3">
            <v-btn icon large style="margin-right: 5px"
              ><v-avatar size="35px">
                <v-icon>mdi-twitter</v-icon>
              </v-avatar></v-btn
            >
            <v-btn icon large
              ><v-avatar size="35px">
                <v-icon>mdi-facebook</v-icon>
              </v-avatar></v-btn
            >

            <v-btn icon large style="margin-left: 5px"
              ><v-avatar size="35px">
                <v-icon>mdi-linkedin</v-icon>
              </v-avatar></v-btn
            >
            <v-btn icon large style="margin-left: 5px"
              ><v-avatar size="35px">
                <v-icon>mdi-reddit</v-icon>
              </v-avatar></v-btn
            >
            <v-btn icon large style="margin-left: 5px"
              ><v-avatar size="35px">
                <v-icon>mdi-email</v-icon>
              </v-avatar></v-btn
            >
            <v-btn icon large style="margin-left: 5px"
              ><v-avatar size="35px">
                <v-icon>mdi-phone</v-icon>
              </v-avatar></v-btn
            >
          </div>
        </v-row>
      </v-container>
    </v-footer>
  </v-app>
</template>

<style>
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Questrial&display=swap%27");

#app {
  font-family: "Nunito Sans", sans-serif;
}
</style>

<style scoped>
.text {
  font-size: 1.1em;
}
</style>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "App",
  data: () => ({}),
  components: {},
  computed: {
    fontSize() {
      return {
        fontSize: this.fontSizeBar + " !important",
      };
    },
    bg() {
      if (this.light) {
        return "background-color: #f2fcff";
      } else {
        return "background-color: #05111f";
      }
    },
    ...mapState(["logged", "username", "light", "fontSizeBar"]),
  },
  methods: {
    exit() {
      this.logout();
      this.$router.push("/sesion");
    },
    ...mapMutations(["logout", "changeLight"]),
  },
};
</script>
