import Vue from "vue";
import Vuex from "vuex";
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    logged: false,
    username: "Prueba",
    name: "Prueba",
    email: "prueba@prueba.com",
    password: "prueba",
    light: true,
    uniqueMovements: [],
    periodicMovements: [],
    auxMov: null,
    colOcio: "#9c27b0",
    colServicios: "#ff9800",
    colCompras: "#4caf50",
    colOtro: "#2196F3",
    fontSizeBar: '16px',
    fontSizeTitle: '32px'
  },
  mutations: {
    login(state, username) {
      state.logged = true;
      state.username = username;
      state.uniqueMovements = [{ "concept": "Nómina", "amount": 1500, "category": "Ingresos", "type": "Ingreso", "date": "2020-09-30", "comment": "", "id": 1 }, { "concept": "Netflix", "amount": 5, "category": "Ocio", "type": "Gasto", "date": "2020-10-05", "comment": "", "id": 2 }, { "concept": "Compra mensual", "amount": 230, "category": "Compras", "type": "Gasto", "date": "2020-10-08", "comment": "", "id": 3 }, { "concept": "Hipoteca", "amount": 375, "category": "Otro", "type": "Gasto", "date": "2020-10-15", "comment": "", "id": 4 }, { "concept": "Venta zapatillas", "amount": 75, "category": "Ingresos", "type": "Ingreso", "date": "2020-10-17", "comment": "", "id": 5 }, { "concept": "Abono transporte", "amount": 45, "category": "Servicios", "type": "Gasto", "date": "2020-10-23", "comment": "", "id": 6 }, { "concept": "Viaje Oporto", "amount": 425, "category": "Ocio", "type": "Gasto", "date": "2020-10-25", "comment": "", "id": 7 }, { "concept": "Comunidad ", "amount": 135, "category": "Servicios", "type": "Gasto", "date": "2020-10-29", "comment": "", "id": 8 }, { "concept": "Nomina", "amount": 1500, "category": "Ingresos", "type": "Ingreso", "date": "2020-10-30", "comment": "", "id": 9 }, { "concept": "Ropa", "amount": 234, "category": "Compras", "type": "Gasto", "date": "2020-10-31", "comment": "", "id": 10 }, { "concept": "Netflix", "amount": 5, "category": "Ocio", "type": "Gasto", "date": "2020-11-05", "comment": "", "id": 11 }, { "concept": "Cumpleaños Jorge", "amount": 50, "category": "Compras", "type": "Gasto", "date": "2020-11-07", "comment": "", "id": 12 }, { "concept": "Hipoteca", "amount": 375, "category": "Otro", "type": "Gasto", "date": "2020-11-15", "comment": "", "id": 13 }, { "concept": "Abono transporte", "amount": 45, "category": "Servicios", "type": "Gasto", "date": "2020-11-23", "comment": "", "id": 14 }, { "concept": "Compra mensual", "amount": 343, "category": "Compras", "type": "Gasto", "date": "2020-11-06", "comment": "", "id": 15 }, { "concept": "Comunidad", "amount": 170, "category": "Servicios", "type": "Gasto", "date": "2020-11-29", "comment": "", "id": 16 }, { "concept": "Nomina", "amount": 1500, "category": "Ingresos", "type": "Ingreso", "date": "2020-11-30", "comment": "", "id": 17 }, { "concept": "Nomina", "amount": 1500, "category": "Ingresos", "type": "Ingreso", "date": "2020-12-30", "comment": "", "id": 18 }, { "concept": "Comunidad", "amount": 140, "category": "Servicios", "type": "Gasto", "date": "2020-12-29", "comment": "", "id": 19 }, { "concept": "Netflix", "amount": 5, "category": "Ocio", "type": "Gasto", "date": "2020-12-05", "comment": "", "id": 20 }, { "concept": "Netflix", "amount": 5, "category": "Ocio", "type": "Gasto", "date": "2021-01-05", "comment": "", "id": 21 }, { "concept": "Compra mensual", "amount": 240, "category": "Compras", "type": "Gasto", "date": "2020-12-03", "comment": "", "id": 22 }, { "concept": "Compra mensual", "amount": 250, "category": "Compras", "type": "Gasto", "date": "2021-01-04", "comment": "", "id": 23 }, { "concept": "Ordenador", "amount": 1700, "category": "Compras", "type": "Gasto", "date": "2020-12-08", "comment": "", "id": 24 }, { "concept": "Viaje Madrid", "amount": 400, "category": "Ocio", "type": "Gasto", "date": "2020-12-21", "comment": "", "id": 25 }, { "concept": "Reparto dividendos", "amount": 1750, "category": "Ingresos", "type": "Ingreso", "date": "2020-12-27", "comment": "", "id": 26 }, { "concept": "Regalos navidad", "amount": 750, "category": "Otro", "type": "Gasto", "date": "2020-12-23", "comment": "", "id": 27 }]
    },
    logout(state) {
      state.logged = false;
      state.username = "Prueba";
      state.name = "Prueba";
      state.email = "prueba@prueba.com";
      state.password = "prueba";
      state.uniqueMovements = [];
      state.periodicMovements = [];
      state.colOcio = "#9c27b0";
      state.colServicios = "#ff9800";
      state.colCompras = "#4caf50";
      state.colOtro = "#2196F3";
      state.fontSizeBar = '16px';
      state.fontSizeTitle = '32px';
    },
    setFontSizeBar(state, size){
      state.fontSizeBar = size + 'px'
    },
    setFontSizeTitle(state, size){
      state.fontSizeTitle = size + 'px'
    },
    setEmail(state, email) {
      state.email = email;
    },
    setName(state, name) {
      state.name = name;
    },
    setUsername(state, username) {
      state.username = username
    },
    setPassword(state, password) {
      state.password = password
    },
    updateProfile(state, name, username, email, password) {
      state.name = name;
      state.username = username;
      state.email = email;
      state.password = password;
    },
    changeLight(state) {
      state.light = !state.light;
    },
    addUniqueMovement(state, movement) {
      if (state.uniqueMovements.length == 0) {
        movement.id = 1;
      } else {
        var lastID = state.uniqueMovements.slice(-1)[0].id;
        movement.id = lastID + 1;
      }
      state.uniqueMovements.push(movement);
    },
    addPeriodicMovement(state, movement) {
      if (state.periodicMovements.length == 0) {
        movement.id = 1;
      } else {
        var lastID = state.periodicMovements.slice(-1)[0].id;
        movement.id = lastID + 1;
      }
      state.periodicMovements.push(movement);
    },
    removeUniqueMovement(state, movementId) {
      for (let [index, movement] of state.uniqueMovements.entries()) {
        if (movement.id == movementId) {
          state.uniqueMovements.splice(index, 1);
        }
      }
    },
    removePeriodicMovement(state, movementId) {
      for (let [index, movement] of state.periodicMovements.entries()) {
        if (movement.id == movementId) {
          state.periodicMovements.splice(index, 1);
        }
      }
    },
    modifyUniqueMovement(state, modifier) {
      //console.log("movement")
      for (let [index, movement] of state.uniqueMovements.entries()) {
        if (movement.id == modifier.id) {
          movement.concept = modifier.concept;
          movement.amount = modifier.amount;
          movement.category = modifier.category;
          movement.type = modifier.type;
          movement.date = modifier.date;
          movement.comment = modifier.comment;
        }
      }
    },
    modifyPeriodicMovement(state, modifier) {
      for (let [index, movement] of state.periodicMovements.entries()) {
        if (movement.id == modifier.id) {
          movement.concept = modifier.concept;
          movement.amount = modifier.amount;
          movement.category = modifier.category;
          movement.type = modifier.type;
          movement.comment = modifier.comment;
          movement.initialDate = modifier.initialDate;
          movement.finalDate = modifier.finalDate;
          movement.freq = modifier.freq;
          movement.freqUnit = modifier.freqUnit;
        }
      }
    },
    getById(state, id) {
      for (let [index, movement] of state.uniqueMovements.entries()) {
        if (movement.id == id) {
          state.auxMov = movement;
        }
      }
    },
    setColOcio(state, color) {
      state.colOcio = color;
    },
    setColServicios(state, color) {
      state.colServicios = color;
    },
    setColCompras(state, color) {
      state.colCompras = color;
    },
    setColOtro(state, color) {
      state.colOtro = color;
    },
  },
  plugins: [createPersistedState()],
  actions: {},
  modules: {},
});
